.App {
  text-align: center;
  /* background-color: #bdd684; */
  background-color: #000;
  height: 100%;
  font-size: 60px;
}

.foods {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.item-toggle {
  transition: background-color 0.3s ease;
  border-radius: 20px;
}

.item-counter {
  display: flex;
}

.item-counter-left {
  background-color: red;
  border-radius: 12px 0 0 12px;
}

.item-counter-right {
  background-color: red;
  border-radius: 0 12px 12px 0;
}

.item-count {
  background-color: white;
  width: 120px;
}

.toggled {
  background-color: #e2f0d6;
}
